<template>
    <v-dialog v-if="images != undefined" style="position: relative;" overlay-color="black" overlay-opacity="0.8" v-model="dialog" persistent>
        <v-carousel height="90vh" :cycle="false" id="galeryCarousel">
                <v-btn id="exitButton" @click="dialog = false" fab icon><i class="fas fa-times fa-3x"></i></v-btn>
                <v-carousel-item
                  v-for="(imgSrc, i) in images"
                  :key="i"
                >
                  <v-img :src="imgSrc" contain max-height="100%"/>
                </v-carousel-item>
              </v-carousel>
   </v-dialog>
</template>

<script>
    import {bus} from '../main.js'
    export default {
      data: () => ({
            dialog: false
      }),
      props: {
            images: Array,
      },
      created () {
          let vm = this;
          bus.$on('dialog', function (value) {
              vm.dialog = value;
          });
      }
    
    }
</script>

<style scoped>
#galeryCarousel{
    width: 100%;
    height: 100%;
}
#exitButton{
    position: absolute;
    color:white;
    left:0vw;
    top:0vw;
    background-color: rgba(107, 107, 107, 0.5);
    border-radius: 10%;
    z-index: 100;
}
</style>